	// Другое
	
    getReaderBook() {
		var url = 'https://xelene.me/books/sources/1/book.html';
		var method = 'GET';
		var async = true;

		var xhr = new XMLHttpRequest();
		xhr.open( method, url, async );
		xhr.send();

		xhr.onreadystatechange = () => {
			if ( xhr.readyState !== 4 ) return;

			if ( xhr.status === 200 ) {
				var response = xhr.responseText;
				this.setState({booktext: response, activeView: "reader", activePanel: "reader"});
			}
		}

		xhr.onerror = ( error ) => {
			console.error( error );
		}
	}

