import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';

import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
var month = "ноября"
function TimeShow(time) {
    if(time === 0) {
        month = "января"
    }
    if(time === 1) {
        month = "февраля"
    }
    if(time === 2) {
        month = "марта"
    }
    if(time === 3) {
        month = "апреля"
    }
    if(time === 4) {
        month = "майя"
    }
    if(time === 5) {
        month = "июня"
    }
    if(time === 6) {
        month = "июля"
    }
    if(time === 7) {
        month = "августа"
    }
    if(time === 8) {
        month = "сентября"
    }
    if(time === 9) {
        month = "октября"
    }
    if(time === 10) {
        month = "ноября"
    }
    if(time === 11) {
        month = "декабря"
    }
}
const bookscomment = ({ id, this: love }) => (
    
    <Panel id={id}>
        <header> </header>
        <div className="titlesbiography"  onClick={() => love.setState({activeView: "home", activePanel: love.state.helpnavigation_array})}>
            <Div>
            <Icon24BrowserBack width={30} height={30} style={{display: "inline-block"}}/> Назад
            </Div>

        </div>
        <div className="errorcomments">
        {
            love.state.selected_book_comments['count'] === 0 ? 
            "Упс, комментариев еще нет!" : null
        }
        </div>
        {
                        love.state.selected_book_comments['items'].map(function(result, i) {
                        return (
                           <Cell
        onClick={
            () => love.openCommentAction(result['user']['first_name'], result['user']['last_name'], result['user']['id'], result['user']['photo_200']) 
            }
        key={i}
        size="l"
        before={<Avatar style={{marginTop: "5px"}} size={55} src={result['user']['photo_200']}/>}
      >
          <div className="namefa">{result['user']['first_name']} {result['user']['last_name']}</div>
           <div className="rating">
            <input type="radio" id="star5" className="rating" /><label title="Rocks!" style={result['rate'] >= 5 ? {color: "#fd7f10"} : null}>5 stars</label>
            <input type="radio" id="star4" className="rating" /><label title="Pretty good" style={result['rate'] >= 4 ? {color: "#fd7f10"} : null}>4 stars</label>
            <input type="radio" id="star3" className="rating" /><label title="Meh" style={result['rate'] >= 3 ? {color: "#fd7f10"} : null}>3 stars</label>
            <input type="radio" id="star2" className="rating" /><label title="Kinda bad" style={result['rate'] >= 2 ? {color: "#fd7f10"} : null}>2 stars</label>
            <input type="radio" id="star1" className="rating" /><label title="Sucks big time" style={result['rate'] >= 1 ? {color: "#fd7f10"} : null}></label>
        </div>
        <div className="commentstime">
            {new Date(result['date'] * 1e3).getDate()} 
            {
            TimeShow(new Date(result['date'] * 1e3).getMonth())
            }
            {month} 
            {new Date(result['date'] * 1e3).getFullYear()} 
        ({new Date(result['date'] * 1e3).getHours()}:{new Date(result['date'] * 1e3).getMinutes()})</div>
        <div className="commentcom">{result['comment']}</div>
      </Cell>
                    
                        )
                })
        }
        
    </Panel>
);
bookscomment.propTypes = {
    id: PropTypes.string.isRequired
};
export default bookscomment;