import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';


// VKUI
import Button from '@vkontakte/vkui/dist/components/Button/Button'
// Icons
import Icon24LikeOutline from '@vkontakte/icons/dist/24/like_outline';
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
import Icon24Copy from '@vkontakte/icons/dist/24/copy';
import Icon24Like from '@vkontakte/icons/dist/24/like';
import Icon24CommentOutline from '@vkontakte/icons/dist/24/comment_outline';
import Icon24ReplyOutline from '@vkontakte/icons/dist/24/reply_outline';

const booksinfo =  ({ id, this: love }) => (
    <Panel id={id}>
        <div>
        <div className="banner">
        <div className="icontitle" onClick={() => love.goBack()}><Icon24BrowserBack width={28} height={28}/></div>
			<img alt="Обложка" className="banner__img" src={love.state.selected_book['cover']}/>
		</div>
        <div className="titlebooksinfo">{love.state.selected_book['name']}</div>
        <div className="authorbooksinfo">{love.state.selected_book_authors_fist} {love.state.selected_book_authors_two}</div>
        <div style={{display: "inline-block"}}>
        <div className="rating" style={{marginLeft: "13px"}}>
            <span className="raytingdal">{love.state.selected_book['rating']}</span>
            <span className="booksinfoview">({love.state.selected_book['rated_count']})</span>
            <input type="radio" id="star5" className="rating" /><label title="Rocks!" style={love.state.selected_book['rating'] >= 5 ? {color: "#fd7f10"} : null}>5 stars</label>
            <input type="radio" id="star4" className="rating" /><label title="Pretty good" style={love.state.selected_book['rating'] >= 4 ? {color: "#fd7f10"} : null}>4 stars</label>
            <input type="radio" id="star3" className="rating" /><label title="Meh" style={love.state.selected_book['rating'] >= 3 ? {color: "#fd7f10"} : null}>3 stars</label>
            <input type="radio" id="star2" className="rating" /><label title="Kinda bad" style={love.state.selected_book['rating'] >= 2 ? {color: "#fd7f10"} : null}>2 stars</label>
            <input type="radio" id="star1" className="rating" /><label title="Sucks big time" style={love.state.selected_book['rating'] >= 1 ? {color: "#fd7f10"} : null}></label>
        </div>
        {love.state.is_rated === false ? 
        <div className="fidbacknone" onClick={() => love.setState({activeModal: "modals", commentforrezention_add: ""})}>Оценить</div>
        :null}
        </div>
        <div className="nonamedivs">
        <Button className="normalbuttonfree" before={<Icon24Copy/>} onClick={() => love.getReaderBook()}>Читать</Button>
        <Button className="normalbuttontwo" onClick={() => love.shareauthor()}><Icon24ReplyOutline/></Button>
        {
            love.state.is_favorite === true ?
            <Button className="normalbuttonone" onClick={() => love.deleteFavourite(love.state.selected_book['id'])}><Icon24Like/></Button>
            :
            <Button className="normalbuttonone" onClick={() => love.addFavourite(love.state.selected_book['id'])}><Icon24LikeOutline/></Button>
        }
        </div>
        <div>
            <div className="titlebooksinfos">О книге</div>
            <div className="textforbooksinfo">
            {love.state.selected_book['annotation']}
            </div>
            <Button className="fidbackcomment" before={<Icon24CommentOutline/>}
        onClick={() => love.commentsGet(love.state.selected_book['id'])}
        >Рецензии</Button>
        </div>
        </div>
    </Panel>
);
booksinfo.propTypes = {
    id: PropTypes.string.isRequired,
    
};
export default booksinfo;