import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Gallery from '@vkontakte/vkui/dist/components/Gallery/Gallery';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Icon24AddOutline from '@vkontakte/icons/dist/24/add_outline';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';
let fruits = [
    "linear-gradient(to right bottom, #c225fc, #ff8d82)", 
    "linear-gradient(to right bottom, #ff5adf, #ff8d82)", 
    "linear-gradient(to right bottom, #5058ff, #00f6ff)", 
    "linear-gradient(to top, #ff5adf, #ff8d82)",
    "linear-gradient(to left, #c225fc, #ff8d82)", 
    "linear-gradient(to left, #ff5adf, #ff8d82)", 
    "linear-gradient(to left, #5058ff, #00f6ff)", 
    "linear-gradient(to bottom, #c225fc, #ff8d82)", 
    "linear-gradient(to bottom, #ff5adf, #ff8d82)", 
    "linear-gradient(to bottom, #5058ff, #00f6ff)", 
    "linear-gradient(to left bottom, #c225fc, #ff8d82)", 
    "linear-gradient(to left bottom, #ff5adf, #ff8d82)",
];
var rand = fruits[Math.floor(Math.random() * fruits.length)];
const home = ({ id, navigator, this: love}) => (
    
    <Panel id={id}>
        <Cell
        style={{marginTop: "5px"}}
        onClick={() => love.getProfile()}
        size="l"
        before={<Avatar style={{marginTop: "5px"}} size={55} src={love.state.photo_200}/>}
      >
          <div className="namehome">{love.state.firstname} {love.state.lastname}</div>
          
        <div className="nearnamehome">История: {(Object.keys(love.state.historylenght).length)}, Избранное: {(Object.keys(love.state.favoritelength).length)}</div>
      </Cell>
        <div className="titless">
            <Div>Разделы</Div>
        </div>
        { love.state.is_group_member === 1 ? 
        <Gallery slideWidth="95%">
    <div className="blockblock" style={{background:  rand}} onClick={() => navigator.go("biography")}>
        <div className="titleblock">Биографии авторов</div>
    </div>
    </Gallery>
        : 
        <Gallery slideWidth="95%">
            <div className="resicle">
            <div className="title">Xelene || Studio</div>
            <Button onClick={() => love.Subscribe()} before={<Icon24AddOutline/>} style={{background: "#00cabe"}}>Подписаться</Button>
        </div>
                
        <div className="blockblock" style={{background:  rand}} onClick={() => navigator.go("biography")}>
			<div className="titleblock">Биографии авторов</div>
		</div>
        </Gallery>
        }
        <div className="titles">
            <Div>Книги</Div>
        </div>
        <div className="SearchMobile__input">
            <input placeholder="Поиск" value={love.state.searchQuery} onChange={love.inputChange}/>
        <div className="SearchMobile__to-filters-wrapper" onClick={() => love.setState({activeModal: 'search'})}>
            <span className="SearchMobile__to-filters"></span>
        </div>
        <div className="SearchMobile__clear-wrapper">
            <span className="SearchMobile__clear"></span>
        </div>
        </div>       
        

                       {love.state.listbooks.map(function(result, i) {
                            return (
                            <div key={i}>
                                <div className="allbooks">
                                    <img onClick={() => love.goBook(result["id"])} alt="обложка" src={result['cover']}/>
                                    <div onClick={() => love.goBook(result["id"])} className="content">
                                    <div onClick={() => love.goBook(result["id"])} className="contenttitle">{result['name']}</div>
                                    <div className="contentreyt">{result['rating']}</div>
                                    <div onClick={() => love.goBook(result["id"])} className="contentauthor">{(result['authors']['0'].first_name)} {(result['authors']['0'].last_name)}</div>
                                    <div onClick={() => love.goBook(result["id"])} className="contentviews">
                                        Просмотров: {result['views']}
                                    </div>
                                </div>
                                <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], formodal_id: result['id'], }); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
                        </div>
                        </div>
                            )
                    })}


    </Panel>
);
home.propTypes = {
    id: PropTypes.string.isRequired,
    this: PropTypes.object.isRequired,
};
export default home;