import React from 'react';
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import HorizontalScroll from '@vkontakte/vkui/dist/components/HorizontalScroll/HorizontalScroll';
const profile = ({ id, this: love}) => (
    <Panel id={id}>
        <div className="profile">
        <div className="titlesbiography"  onClick={() => love.goBack()}>
            <Icon24BrowserBack width={30} height={30} style={{display: "inline-block"}}/> Назад

        </div>
        <br/>
        <br/>
        <div className="titless">
            Избранное
        </div>
        <br/>
        <HorizontalScroll>
          <div style={{ display: 'flex' }}>
            <div style={{paddingLeft: 4 }}>
            {love.state.booksfavorite_array.map(function(result, i) {
                            return (
                                <div key={i}>
                                <div className="allbooksprofile">
                                    <img onClick={() => love.goBook(result["id"])} alt="обложка" src={result['cover']}/>
                                    <div onClick={() => love.goBook(result["id"])} className="content">
                                    <div onClick={() => love.goBook(result["id"])} className="contenttitle">{result['name']}</div>
                                    <div className="contentreyt">{result['rating']}</div>
                                    <div onClick={() => love.goBook(result["id"])} className="contentauthor">{(result['authors']['0'].first_name)} {(result['authors']['0'].last_name)}</div>
                                    <div onClick={() => love.goBook(result["id"])} className="contentviews">
                                        Просмотров: {result['views']}
                                    </div>
                                </div>
                        </div>
                        </div>
                            )
                    })}
           </div>
          </div>
        </HorizontalScroll>
                    <br/>
        <div className="titless">
            История
        </div>
        <HorizontalScroll>
          <div style={{ display: 'flex' }}>
            <div style={{paddingLeft: 4 }}>
            {love.state.bookshistory_array.map(function(result, i) {
                            return (
                                <div key={i}>
                                <div className="allbooksprofile">
                                    <img onClick={() => love.goBook(result["id"])} alt="обложка" src={result['cover']}/>
                                    <div onClick={() => love.goBook(result["id"])} className="content">
                                    <div onClick={() => love.goBook(result["id"])} className="contenttitle">{result['name']}</div>
                                    <div className="contentreyt">{result['rating']}</div>
                                    <div onClick={() => love.goBook(result["id"])} className="contentauthor">{(result['authors']['0'].first_name)} {(result['authors']['0'].last_name)}</div>
                                    <div onClick={() => love.goBook(result["id"])} className="contentviews">
                                        Просмотров: {result['views']}
                                    </div>
                                </div>
                        </div>
                        </div>
                            )
                    })}
           </div>
          </div>
        </HorizontalScroll>
        </div>
    </Panel>
);

export default profile;