import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Div from '@vkontakte/vkui/dist/components/Div/Div';

import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const biography = ({id, navigator, this: love}) => (
    <Panel id={id}>
        <header> </header>
        <div className="titlesbiography"  onClick={() => navigator.goBack()}>
            <Div>
            <Icon24BrowserBack width={30} height={30} style={{display: "inline-block"}}/> Авторы
            </Div>
        </div>    
        <div className="bookspopular" onClick={() => navigator.go("authorinfo")} >
            <img alt="обложка" src="http://1ul.ru/upload/file/publication/Pushkin.jpg"/>
            <div className="coslet">
            <div className="contenttitle">А.С. Пушкин</div>
            <div className="contentauthor">хх.хх.хххх</div>
        </div>
</div>

<div className="bookspopular" onClick={() => navigator.go("authorinfo")}>
<img alt="обложка" src="https://img.labirint.ru/images/upl/descripts/pic_1532423481.png"/>
<div className="coslet">
<div className="contenttitle">В.В. Яковлев</div>
            <div className="contentauthor">хх.хх.хххх</div>
</div>


</div>
<div className="bookspopular" onClick={() => navigator.go("authorinfo")} >
<img alt="обложка" src="https://stuki-druki.com/biofoto2/Vadim-Yakovlev-01.jpg"/>
<div className="coslet">
<div className="contenttitle">Н.И. Фадеев</div>
            <div className="contentauthor">хх.хх.хххх</div>
</div>
</div>
    </Panel>
);
biography.propTypes = {
    id: PropTypes.string.isRequired,
    this: PropTypes.object.isRequired,
};
export default biography;