import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';


// VKUI
import Div from '@vkontakte/vkui/dist/components/Div/Div'
// Icons
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const booksinfo =  ({ id, navigator, this: love }) => (
    <Panel id={id}>
        <header> </header>
        <div className="titlesbiography"  onClick={() => navigator.goBack()}>
            <Div>
            <Icon24BrowserBack width={30} height={30} style={{display: "inline-block"}}/> Назад
            </Div>
        </div>
        <div className="anotherblock">
          <div className="anotherinfo">
            <div></div>
            <div className="anothertitle">Александр Пушин</div>
            <div className="anotherbutton" onClick={() => love.shareauthor()}>
                <img className="anotherbuttondown" src="https://img.icons8.com/ios/50/000000/share.png" alt=""/>
            </div>
            <div className="anotherannotation">Поэт</div>
            <div className="prostoline"></div>
            <div className="anothermiddletext">
            Алекса́ндр Серге́евич Пу́шкин — русский поэт, драматург и прозаик, заложивший основы русского реалистического направления, критик и теоретик литературы, историк, публицист; один из самых авторитетных литературных деятелей первой трети XIX века.
            </div>
            <div className="anotherblockforhard">
            <div className="anotherhardinfo">Родился: <span>9 ноября 1818 г., Орёл</span></div>
            <div className="anotherhardinfo">Умер: <span>3 сентября 1883 г., Буживаль, Франция</span></div>
            <div className="anotherhardinfo" >Годы творчества: <span>1834—1883</span></div>
            <div className="anotherhardinfo">Язык произведений: <span>русский</span></div>
            </div>
          </div>
        </div>
    </Panel>
);
booksinfo.propTypes = {
    id: PropTypes.string.isRequired
};
export default booksinfo;